<template>
    <div class="container">
        <TopButton title="" />
        <div class="profile">
            <img src="@/assets/img/user.svg" alt="user" />
            <div class="title">
                <!--      -->
                <template v-if="isLoggedIn"> {{ userName }} . </template>
                <template v-else>  . </template>
            </div>
            <!--      -->
            <a class="login" @click="toggleLogin">
                {{ isLoggedIn ? ' >' : ' >' }}
            </a>
        </div>
        <div class="stats">
            <div>
                <a class="link point">
                    
                    <strong>0P</strong>
                </a>
            </div>
            <div>
                <a class="link coupon">
                    
                    <strong>0</strong>
                </a>
            </div>
            <div>
                <a class="link order">
                    
                    <strong>0</strong>
                </a>
            </div>
            <div>
                <a class="link delivery">
                    
                    <strong>0</strong>
                </a>
            </div>
            <div>
                <a class="link evaluation">
                     
                    <strong>0</strong>
                </a>
            </div>
            <div>
                <a class="link inquiry">
                    
                    <strong>0</strong>
                </a>
            </div>
        </div>
        <div class="section">
            <ul class="event">
                
                <li>  / </li>
                <li>  </li>
            </ul>
            <ul class="service">
                
                <li> </li>
                <li> </li>
                <li>Q & A</li>
                <li>1:1 </li>
                <li></li>
            </ul>
        </div>
        <div class="weather-container">
            <div class="weatherBox" v-if="weather && weatherIcon">
                <div class="weatherBox-lt">
                    <div><img :src="weatherIcon" :alt="weatherIcon" /></div>
                    <div>
                        <p>{{ weather.temp }}</p>
                    </div>
                </div>
                <div class="weatherBox-rt">
                    <p>
                        {{ weather.temp_max }}  <br />
                        {{ weather.temp_min }} 
                    </p>
                    <p>
                         <span :style="{ color: airConditionColor }">{{ airCondition }}</span>
                    </p>
                </div>
            </div>
            <div class="weatherBox" v-else>
                <p>   ...</p>
            </div>
        </div>
    </div>
    <div class="margin"></div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';
import { ref, onMounted } from 'vue';

export default {
    name: 'Mypage',
    components: {
        TopButton
    },
    data() {
        return {
            isLoggedIn: false, //   
            userName: '' //   
        };
    },
    methods: {
        Login() {
            this.$router.push('/login');
        },
        async toggleLogin() {
            if (this.isLoggedIn) {
                //  
                try {
                    const response = await fetch('/api/auth/logout.php', {
                        method: 'POST',
                        credentials: 'include' //  
                    });
                    const data = await response.json();
                    console.log('Logout Response:', data); // 

                    if (data.status === 'success') {
                        this.isLoggedIn = false;
                        this.userName = '';
                        alert('.');
                    } else {
                        alert(data.message || ' ');
                    }
                } catch (error) {
                    console.error('   :', error);
                    alert('    ');
                }
            } else {
                //   
                this.$router.push('/login');
            }
        },
        async checkLoginStatus() {
            try {
                const response = await fetch('/api/auth/check_login.php', {
                    method: 'GET',
                    credentials: 'include' //  
                });
                console.log('Response Status:', response.status); // 
                const data = await response.json();
                console.log('Response Data:', data); // 

                //    ( logged_in )
                if (data.logged_in) {
                    this.isLoggedIn = true;
                    this.userName = data.username;
                } else {
                    console.log(' :', data);
                    this.isLoggedIn = false;
                    this.userName = '';
                }
            } catch (error) {
                console.error('    :', error);
            }
        }
    },
    mounted() {
        //    
        this.checkLoginStatus();
    },
    setup() {
        //   
        const weather = ref(null); //    
        const weatherIcon = ref(''); //   URL
        const airCondition = ref(''); //  
        const airConditionColor = ref(''); //     

        /*    */
        const fetchWeatherData = async () => {
            try {
                const weatherResponse = await fetch(
                    'https://api.openweathermap.org/data/2.5/weather?lat=37.2992437&lon=126.9782357&appid=822e1d81a695655b411c939d26499c1f&units=Metric'
                );
                const weatherData = await weatherResponse.json(); //   JSON 
                //   'weather' 
                weather.value = {
                    temp: weatherData.main.temp, //  
                    temp_min: weatherData.main.temp_min, //  
                    temp_max: weatherData.main.temp_max //  
                };

                //    
                const iconCode = weatherData.weather[0].icon;
                weatherIcon.value = `https://openweathermap.org/img/wn/${iconCode}@2x.png`;
            } catch (error) {
                console.error('     : ', error);
            }
        };

        /*     */
        const fetchAirQualityData = async () => {
            try {
                const airResponse = await fetch(
                    'http://api.openweathermap.org/data/2.5/air_pollution?lat=37.5070431&lon=126.8902185&appid=822e1d81a695655b411c939d26499c1f'
                );
                const airData = await airResponse.json();

                const fineDust = airData.list[0].components.pm2_5;

                if (fineDust <= 15) {
                    airCondition.value = '';
                    airConditionColor.value = '#0085FF';
                } else if (fineDust > 16 && fineDust <= 35) {
                    airCondition.value = '';
                    airConditionColor.value = '#009806';
                } else if (fineDust > 36 && fineDust <= 75) {
                    airCondition.value = '';
                    airConditionColor.value = '#FF2B1E';
                } else {
                    airCondition.value = ' ';
                    airConditionColor.value = '#B90B00';
                }
            } catch (error) {
                console.error('     : ', error);
            }
        };
        //     api
        onMounted(() => {
            //   
            fetchWeatherData();
            //   
            fetchAirQualityData();
        });

        //   
        return {
            weather,
            weatherIcon,
            airCondition,
            airConditionColor
        };
    }
};
</script>
<style scoped>
.container {
    text-align: center;
}

.profile {
    height: 100px;
    margin-top: 25px;
    line-height: 2rem;
}

.profile img {
    width: 85px;
    height: 85px;
    float: left;
    object-fit: contain;
    padding-top: 11px;
    border-radius: 50%;
    border: 1px solid #000;
    margin: 0 15px 0 10px;
}

.profile .title {
    float: left;
    margin-top: 13px;
    font-size: 1.3rem;
    font-weight: bold;
    color: #555;
    line-height: 30px;
}

.login {
    float: left;
    font-size: 1.1rem;
    text-decoration: none;
    color: #333;
    font-weight: bold;
    cursor: pointer;
}

.stats {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3px;
    margin: 10px 0 20px 0;
}

.stats div {
    padding: 5px;
    font-size: 0.9rem;
}

.link {
    cursor: pointer;
    text-decoration: none;
    color: #000;
}

.stats div:nth-child(3n-1) {
    border-left: 1px solid #000;
    border-right: 1px solid #000;
}
.stats div:nth-child(-n + 3) {
    border-bottom: 1px solid #000;
}

.stats div strong {
    display: block;
    font-size: 20px;
    margin-bottom: 5px;
}

.section {
    width: 100%;
    text-align: left;
}

.section ul {
    list-style: none;
    font: 0.9rem;
    font-weight: 700;
    color: #777;
    line-height: 2rem;
    margin-bottom: 20px;
    letter-spacing: -0.07rem;
    padding-left: 20px;
}

.section ul li {
    font-size: 1.1rem;
    color: #000;
}

/*  */

.weather-container {
    width: 100%;
    margin: 0 auto;
}

.weatherBox-lt div {
    display: block;
    float: left;
}

.weatherBox-lt img {
    float: left;
    width: 60px;
}

.weatherBox-lt p {
    font-size: 1.3rem;
    line-height: 60px;
    font-weight: 700;
}

.weatherBox-rt {
    float: right;
    margin: 7px 10px;
}

.weatherBox-rt p {
    float: left;
    font-size: 0.9rem;
    text-align: left;
    font-weight: 600;
    margin-left: 7px;
}

.margin {
    height: 77px;
}
</style>
