<template>
    <div class="container">
        <TopButton title="" />
        <div v-if="isLoading">  ...</div>
        <div v-else>
            <button type="button" @click="navigateToCreate"></button>
            <!--   -->
            <div class="contents">
                <div
                    class="reviewItem"
                    v-for="review in reviews"
                    :key="review.id"
                    @click="navigateToDetail(review.user_id)"
                >
                    <div class="reviewImg">
                        <img :src="review.image_path" alt=" " v-if="review.image_path" />
                    </div>
                    <div class="reviewContent">
                        <p> : {{ review.title }}</p>
                        <p><img src="@/assets/img/leaf.svg" alt="score" /> 4.3</p>
                        <p>{{ review.content }}</p>
                        <p> : {{ review.username || '' }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ReviewList',
    components: {
        TopButton
    },
    data() {
        return {
            reviews: [], //   
            isLoading: false //   
        };
    },
    methods: {
        //   
        async fetchReviews() {
            this.isLoading = true; //  
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/posts/fetch_reviews.php');
                if (!response.ok) {
                    throw new Error(`    .  : ${response.status}`);
                }
                this.reviews = await response.json();
            } catch (error) {
                console.error('     :', error);
                alert('    .  .');
            } finally {
                this.isLoading = false; //  
            }
        },
        //    
        navigateToCreate() {
            this.$router.push('/reviews/create');
        },
        //    
        navigateToDetail(reviewId) {
            this.$router.push(`/reviews/${reviewId}`);
        }
    },
    mounted() {
        this.fetchReviews(); //      
    }
};
</script>

<style scoped>
/*  */
.contents {
    width: 100%;
    margin: 0 auto;
}
.reviewItem {
    width: 90%;
    display: flex;
    margin: 20px;
    cursor: pointer;
    transition: transform 0.2s;
}
.reviewItem:hover {
    transform: scale(1.02);
}
.reviewImg {
    width: 125px;
    height: 150px;
    margin-right: 20px;
}
.reviewImg img {
    display: block;
    width: 125px;
    height: 150px;
}
.reviewContent {
    text-align: left;
}
.reviewContent p {
    margin: 0;
}
</style>
