<template>
    <div class="container">
        <TopButton title="" />
        <div class="check_box">
            <div class="check">
                <input type="checkbox" value="" id="check" checked />
                <label for="check">  </label>
            </div>
            <div class="checkdelete">
                <button><span></span></button>
                <button><span></span></button>
            </div>
        </div>
        <div class="contents" v-if="cart.length > 0">
            <div class="contentBox" v-for="item in cart" :key="item.id">
                <div class="itemName">
                    <input type="checkbox" value="" id="name" checked />
                    <label for="name"> {{ item.title }} </label>
                </div>
                <div class="itemBox">
                    <div class="itemImg">
                        <img :src="item.image" alt=" " />
                    </div>
                    <div class="itemOptions">
                        <p>{{ item.price }} </p>
                        <p>1 </p>
                        <p></p>
                        <div class="numBox">
                            <p></p>
                            <button @click="decrease(item)">-</button>
                            <span>{{ item.quantity }}</span>
                            <button @click="increase(item)">+</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-else class="v-else">
            <p>  .</p>
        </div>
        <div class="buyBox" v-if="cart.length > 0">
            <div class="buyContent">
                <p> {{ totalQuantity }} </p>
                <p>{{ totalPrice }} <span></span></p>
            </div>
            <div class="buyButton">
                <button @click="goToCheckout"></button>
            </div>
        </div>
    </div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ShoppingCart',
    components: {
        TopButton
    },
    props: ['product'], //  params   props 
    data() {
        return {
            cart: [] //  
        };
    },
    created() {
        // query     
        console.log(' JSON :', this.$route.query.product);
        const productQuery = this.$route.query.product;
        if (productQuery) {
            const productData = JSON.parse(productQuery); // JSON  
            const existingItem = this.cart.find((item) => item.id === productData.id);
            if (existingItem) {
                existingItem.quantity++;
            } else {
                this.cart.push({ ...productData, quantity: 1 });
            }
        }
    },
    computed: {
        //   
        totalPrice() {
            return this.cart.reduce((total, item) => total + item.price * item.quantity, 0);
        },
        //   
        totalQuantity() {
            return this.cart.reduce((total, item) => total + item.quantity, 0);
        }
    },
    methods: {
        //  
        decrease(item) {
            if (item.quantity > 1) {
                item.quantity--;
            }
        },
        //  
        increase(item) {
            item.quantity++;
        },
        goToCheckout() {
            //    
            this.$router.push({
                name: 'Checkout',
                query: { cart: JSON.stringify(this.cart) } // JSON    
            });
        }
    }
};
</script>
<style scoped>
