    border: none;
    background: none;
    margin-right: 10px;
    padding: 0;
}

/*   */
.contents {
    margin: 0 auto;
    width: 95%;
    height: 100vh;
    margin-top: 15px;
    border: 1px solid #999;
    border-radius: 10px 10px 0 0;
}

.itemName {
    letter-spacing: -1px;
}

.itemName input {
    margin-right: 5px;
}

.contents .contentBox {
    margin: 0 auto;
    width: 90%;
    height: 200px;
    margin-top: 25px;
    border-bottom: 1px solid #999;
}

.contents .contentBox .itemBox {
    width: 100%;
    margin: 15px 0;
}
.contents .contentBox .itemBox .itemImg {
    width: 137px;
    height: 137px;
    background-color: #999;
    margin-right: 20px;
    float: left;
}
.contents .contentBox .itemBox .itemImg img {
    width: 137px;
    height: 137px;
}
.itemOptions {
    text-align: left;
    line-height: 1.3rem;
}
.itemOptions p {
    margin: 5px;
    margin-bottom: 7px;
    text-align: left;
    float: left;
}

.itemOptions p:first-child {
    font-weight: bold;
    font-size: 1.2rem;
}

.itemOptions .numBox {
    width: 143px;
    margin-top: 5px;
