    line-height: 20px;
    background: #ececec;
    text-align: center;
    padding: 3px 0;
    border-radius: 3px;
}

.itemOptions .numBox p {
    font-size: 0.95rem;
    margin-left: 13px;
}

.numBox button {
    width: auto;
    font-size: 1.1rem;
    margin: 3px 0;
    border: none;
    background: none;
}

.itemOptions span {
    font-weight: bold;
