<template>
    <button @click="goBack">
        <img src="../assets/img/chevron-left-solid.svg" alt="" width="20" height="20" />
    </button>
    <div class="login-container">
        <div class="logo">
            <img src="../assets/img/nede_3.jpg" alt="NEDE logo" />
        </div>

        <form @submit.prevent="handleLogin">
            <input type="text" v-model="form.username" placeholder="" required />
            <input
                type="password"
                v-model="form.password"
                placeholder="(8~10, ++)"
                required
            />
            <div class="options">
                <label> <input type="checkbox" v-model="form.autoLogin" />  </label>
                <label> <input type="checkbox" v-model="form.saveId" />   </label>
            </div>

            <button type="submit" :disabled="loading"></button>
            <button type="button" @click="Easylogin"> </button>

            <div class="links">
                <button @click="navigateTo('/register')"></button>
                <button @click="navigateTo('#')"> </button>
                <button @click="navigateTo('#')"> </button>
            </div>
        </form>

        <footer>
            <span>ver.1.1.1 </span>
            <span></span>
            <span></span>
        </footer>
    </div>
</template>

<script>
export default {
    data() {
        return {
            form: {
                username: '',
                password: '',
                autoLogin: false,
                saveId: false
            },
            loading: false,
            message: ''
        };
    },
    methods: {
        async handleLogin() {
            if (!this.form.username || !this.form.password) {
                alert('  .');
                return;
            }

            this.loading = true;
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/auth/login.php', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        credentials: 'include'
                    },
                    body: JSON.stringify(this.form)
                });

                const result = await response.json();
                if (result.status === 'success') {
                    alert(' !');
                    this.$router.push('/mypage'); //   
                } else {
                    alert(result.message || ' !');
                }
            } catch (error) {
                alert('    .');
                console.error('Error:', error);
            } finally {
                this.loading = false;
            }
        },
        Easylogin() {
            this.$router.push('/easylogin');
        },
        navigateTo(path) {
            this.$router.push(path);
        },
        goBack() {
            this.$router.go(-1); // 
        }
    }
};
</script>

<style scoped>
.login-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    font-family: Arial, sans-serif;
}

.logo img {
    max-width: 100px;
    margin-bottom: 20px;
}

form {
    display: flex;
    flex-direction: column;
}

input,
button {
    margin-bottom: 15px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

button {
    background-color: #333;
    color: white;
    border: none;
    cursor: pointer;
}

button:disabled {
    background-color: #aaa;
    cursor: not-allowed;
}

.options {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.links {
    display: flex;
    justify-content: space-between;
}

footer {
    margin-top: 20px;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    color: #555;
}
</style>
