<template>
    <div class="container">
        <TopButton title="" />
        <div v-if="isLoading">  ...</div>
        <div v-else class="review-container">
            <div class="buttons" v-if="isOwner">
                <button type="button" @click="navigateToEdit"></button>
                <button type="button" @click="deleteReview"></button>
            </div>
            <div class="reviewImg">
                <img :src="review.image_path" alt="" v-if="review.image_path" />
            </div>
            <div class="review-content">
                <p> : {{ review.username }}</p>
                <p><img src="@/assets/img/leaf.svg" alt="score" /> 4.3</p>
                <p> : {{ review.title }}</p>
                <p>{{ review.content }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';
export default {
    name: 'ReviewPage',
    components: { TopButton },
    data() {
        return {
            review: {}, //   
            loggedInUser: null, //   ID
            isOwner: false, //   
            isLoading: true //  
        };
    },
    methods: {
        //   
        async fetchReview() {
            this.isLoading = true;
            const reviewId = this.$route.params.id; // URL   ID 
            try {
                const response = await fetch(
                    `http://green609b.dothome.co.kr/api/posts/fetch_review.php?id=${reviewId}`
                );
                if (!response.ok) {
                    throw new Error(`   .  : ${response.status}`);
                }
                const data = await response.json();
                this.review = data;

                //    
                this.isOwner = this.review.username === this.loggedInUser;
            } catch (error) {
                console.error('     :', error);
                alert('   .  .');
            } finally {
                this.isLoading = false;
            }
        },
        //    
        async fetchLoggedInUser() {
            try {
                const response = await fetch('http://green609b.dothome.co.kr/api/auth/check_login.php', {
                    method: 'GET',
                    credentials: 'include' //   
                });
                const result = await response.json();
                if (result.logged_in) {
                    this.loggedInUser = result.username; //   ID 
                    console.log('Logged-in user ID:', this.loggedInUser); //  
                } else {
                    this.loggedInUser = null;
                }
            } catch (error) {
                console.error('      :', error);
            }
        },
        //   
        navigateToEdit() {
            this.$router.push(`/reviews/edit/${this.review.user_id}`); //   
        },
        //  
        async deleteReview() {
            if (confirm(' ?')) {
                try {
                    const response = await fetch(`http://green609b.dothome.co.kr/api/posts/delete_review.php`, {
                        method: 'POST',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify({ id: this.review.user_id })
                    });
                    const result = await response.json();
                    alert(result.message);
                    if (result.status === 'success') {
                        this.$router.push('/reviews'); //     
                    }
                } catch (error) {
                    console.error('    :', error);
                }
            }
        }
    },
    async mounted() {
        await this.fetchLoggedInUser(); //    
        await this.fetchReview(); //   
    }
};
</script>

<style scoped>
.review-container {
    padding: 20px;
}
.buttons {
    margin-bottom: 20px;
}
.reviewImg img {
    max-width: 100%;
    height: auto;
}
</style>

<style scoped>
.container {
    width: 100%;
    height: 100%;
}
.buttons {
    text-align: right;
    margin-top: 15px;
}
.reviewImg {
    background-color: #999;
    margin: 15px auto;
    width: 350px;
    height: 320px;
}
.reviewImg img {
    display: block;
    width: 350px;
    height: 320px;
}
.review-content {
    margin: 10px auto;
    width: 350px;
}
</style>
