<template>
    <div class="login-page">
        <h1> </h1>

        <!--   -->
        <div>
            <button v-if="!isLoggedIn.google" @click="googleLogin" class="google-button">
                <img src="@/assets/img/googlelogin.png" alt="Login with Google" class="google-button-img" />
            </button>
            <button v-if="isLoggedIn.google" @click="googleLogout" class="logout-button">Google Logout</button>
        </div>

        <!--   -->
        <div>
            <a id="kakao-login-btn" v-if="!isLoggedIn.kakao" @click="kakaoLogin">
                <img
                    src="https://k.kakaocdn.net/14/dn/btroDszwNrM/I6efHub1SN5KCJqLm1Ovx1/o.jpg"
                    width="222"
                    alt="  "
                />
            </a>
            <button v-if="isLoggedIn.kakao" @click="kakaoLogout" class="logout-button">Kakao Logout</button>
        </div>

        <!--   -->
        <div>
            <!--   accessToken     ,    .    isLoggedIn.naver    -->
            <!--  :    accessToken  . accessToken      ,       .-->
            <!--    (isLoggedIn.naver) (accessToken)    . -->
            <div id="naverIdLogin" v-if="!isLoggedIn.naver"></div>
            <button v-if="isLoggedIn.naver" @click="naverLogout" class="logout-button">Naver Logout</button>
        </div>
    </div>
</template>

<script>
// import { ref } from 'vue';
import { googleSdkLoaded } from 'vue3-google-login';

export default {
    data() {
        return {
            isLoggedIn: {
                google: false,
                kakao: false,
                naver: false
            },
            naverLogin: null, //   
            naverAccessToken: null //    
        };
    },
    mounted() {
        //  SDK 
        if (!window.Kakao.isInitialized()) {
            window.Kakao.init('b65d155bd8d1bfb900dca929ae8afabc');
        }

        //    
        this.initializeNaverLoginButton();
    },
    methods: {
        /**   */
        googleLogin() {
            if (this.isLoggedIn.kakao || this.isLoggedIn.naver) {
                alert('    .  .');
                return;
            }
            googleSdkLoaded((google) => {
                google.accounts.oauth2
                    .initCodeClient({
                        client_id: process.env.VUE_APP_OAUTH_CLIENT,
                        scope: 'email profile openid',
                        callback: (response) => {
                            console.log('Google Login Response:', response);
                            this.isLoggedIn.google = true;
                        }
                    })
                    .requestCode();
            });
        },
        googleLogout() {
            console.log('Google Logout');
            this.isLoggedIn.google = false;
        },

        /**   */
        kakaoLogin() {
            if (this.isLoggedIn.google || this.isLoggedIn.naver) {
                alert('    .  .');
                return;
            }
            window.Kakao.Auth.login({
                scope: 'profile_image, profile_nickname',
                success: this.getKakaoAccount
            });
        },
        getKakaoAccount() {
            window.Kakao.API.request({
                url: '/v2/user/me',
                success: (res) => {
                    console.log('Kakao Login Response:', res);
                    this.isLoggedIn.kakao = true;
                },
                fail: (error) => {
                    alert('  ');
                    console.error(error);
                }
            });
        },
        kakaoLogout() {
            window.Kakao.Auth.logout(() => {
                console.log('Kakao Logout');
                this.isLoggedIn.kakao = false;
            });
        },

        /**   */
        initializeNaverLoginButton() {
            if (this.isLoggedIn.google || this.isLoggedIn.kakao) {
                alert('    .  .');
                return;
            }
            this.naverLogin = new window.naver.LoginWithNaverId({
                clientId: 'MyEYpin_VSCD218l6SHu',
                callbackUrl: 'http://green609b.dothome.co.kr/easylogin',
                isPopup: false,
                loginButton: { color: 'green', type: 3, height: 60 }
            });
            this.naverLogin.init(); //  SDK 

            //   
            this.naverLogin.getLoginStatus((status) => {
                if (status) {
                    this.checkNaverLoginStatus();
                }
            });
        },
        checkNaverLoginStatus() {
            //    
            this.naverLogin.getLoginStatus((status) => {
                if (status) {
                    //        
                    this.naverAccessToken = this.naverLogin.accessToken.accessToken;
                    console.log('Naver Login Success:', this.naverLogin.user); //    ()
                    this.isLoggedIn.naver = true;
                } else {
                    console.log('Naver Login Failed');
                }
            });
        },
        naverLogout() {
            if (!this.naverAccessToken) {
                console.log('  .');
                return;
            }
            //   URL
            const logoutUrl = `https://nid.naver.com/oauth2.0/token?grant_type=delete&client_id=MyEYpin_VSCD218l6SHu&client_secret=kkCdziVPyr&access_token=${this.naverAccessToken}&service_provider=NAVER`;

            // iframe   
            const iframe = document.createElement('iframe');
            iframe.style.display = 'none';
            iframe.src = logoutUrl;
            document.body.appendChild(iframe);

            //     
            this.naverAccessToken = null;
            this.isLoggedIn.naver = false;

            // DOM     
            this.$nextTick(() => {
                this.initializeNaverLoginButton();
            });
            console.log('  ');
        }
    }
};
</script>

<style scoped>
.login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.google-button,
.logout-button {
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}

.google-button:hover,
.logout-button:hover {
    background-color: #357ae8;
}
</style>
